import React, { Component } from 'react';

export class ForTheNews extends Component {
  constructor(props) {
    super(props);
    this.news = [
      {
        "img": 'img-1',
        "title": 'World Cup 2018 draw: Spain and Portugal in same group',
        "date": '01 Jan 2018',
        "description": 'Neighbours Spain and Portugal will meet at next year\'s Fifa World Cup after being drawn. Friday\'s draw in Moscow.'
      },
      {
        "img": 'img-2',
        "title": 'Burnley midfielder out for the rest of the season',
        "date": '01 Jan 2018',
        "description": 'Neighbours Spain and Portugal will meet at next year\'s Fifa World Cup after being drawn. Friday\'s draw in Moscow.'
      },
      {
        "img": 'img-3',
        "title": 'World Cup 2018 draw: Spain and Portugal in same group',
        "date": '01 Jan 2018',
        "description": 'Neighbours Spain and Portugal will meet at next year\'s Fifa World Cup after being drawn. Friday\'s draw in Moscow.'
      },
      {
        "img": 'img-4',
        "title": 'Burnley midfielder out for the rest of the season',
        "date": '01 Jan 2018',
        "description": 'Neighbours Spain and Portugal will meet at next year\'s Fifa World Cup after being drawn. Friday\'s draw in Moscow.'
      }
    ]
  }

  render() {
    return (
      <div className="news">
        <div className="container">
          <h2 className="text-center news__title">From The <span className="font-weight-bold">News</span></h2>
          <div className="row">
            {
              this.news.map((item, key) => {
                return (
                  <>
                    <div className="col-md-6 col-xl-3 news__item" key={ key }>
                      <img src={ "/img/news/" + item.img + ".jpg" } alt={ this.imgTitle } className="news__item-img" />
                      <h6 className="font-weight-bold news__item-title">{ item.title }</h6>
                      <p className="news__item-date font-weight-light mb-1">{ item.date }</p>
                      <p className="news__item-description mb-1">{item.description}</p>
                      <a href="javascript:void(0);" className="btn btn-danger font-weight-light news__item-btn">Learn More</a>
                    </div>
                  </>
                )
              })
            }
          </div>
        </div>
      </div>
    );
  }
}
