import React, { Component } from 'react';

export class EconomicModel extends Component {
  constructor(props) {
    super(props);
    this.steps = [
      {
        "icon": 'pieces',
        "text": 'A sport gaming and <br /> betting currency'
      },
      {
        "icon": 'reward',
        "text": 'As a reward'
      },
      {
        "icon": 'payment',
        "text": 'A form of payment'
      },
      {
        "icon": 'histogram',
        "text": 'An incentive which powers <br /> the bitgoals economy'
      }
    ],
    this.charts = [
      {
        "id": 1,
        "percent": 40,
        "text": 'Marketing',
        "textPosX": 390,
        "textPosY": 190,
        "path": 'M 227, 227 L 227 0 A 227, 227 0 0 1 360, 410 Z 227 227'
      },
      {
        "id": 2,
        "percent": 30,
        "text": 'Partners',
        "textPosX": 55,
        "textPosY": 130,
        "path": 'M 227,227 L 360 410 A 227, 227 0 0 1 11, 297 Z 227 227'
      },
      {
        "id": 3,
        "percent": 30,
        "text": 'Development',
        "textPosX": 160,
        "textPosY": 410,
        "path": 'M 227,227 L 11 297 A 227, 227 0 0 1 227, 0 Z 227 227'
      }
    ]
  }

  render() {
    return (
      <div className="section bg-light economic-model">
        <div className="container">
          <h2 className="text-center economic-model__title">Economic <span className="font-weight-bold">Model</span></h2>
          <div className="row no-gutters">
            <div className="col-lg-6 d-flex flex-column align-items-center pb-2 pb-lg-0 p-0 pr-md-2 mb-md-0 economic-model__chart">
              <svg fill="white" className="economic-model__chart-svg" viewBox="0 0 454 454">
                {
                  this.charts.map((chart, key) => {
                    return (
                      <>
                        <path className={"for-detail-" + chart.id } d={ chart.path } key={ key } />
                      </>
                    )
                  })
                }
                {
                  this.charts.map((chart, key) => {
                    return (
                      <>
                        <text x={ chart.textPosX } y={ chart.textPosY } fontSize="21" fill="#fff" key={ key }>{ chart.percent + "%" }</text>
                      </>
                    )
                  })
                }
                <path d="
                  M 80, 227
                  a 147,147 0 1 0 294,0
                  a 147,147 0 1 0 -294,0
                " fill="white" />
                <text x="180" y="205" fontWeight="bold" fontSize="27" fill="#000">FUNDS</text>
                <text x="113" y="175" fontWeight="bold" fontSize="37" fill="#000" transform="scale(1, 1.5)">ALLOCATION</text>
              </svg>
              <div className="economic-model__chart-list">
                {
                  this.charts.map((chart, key) => {
                    return (
                      <>
                        <div className={ "text-uppercase font-weight-bold economic-model__chart-item for-detail-" + chart.id } key={ key }>{ chart.text }</div>
                      </>
                    )
                  })
                }
              </div>
            </div>
            <div className="col-lg-6">
              <p className="economic-model__description pr-lg-1">The Bitgоаlѕ Platform will be based on its STP tоkеn. The STP token iѕ iѕѕuеd on dеmаnd аѕ соntributiоn tаkеѕ place, i.e. аѕ 0.6 STP is ѕwарреd fоr еithеr ETH, BTC оr BCH, an аdditiоnаl 0.4 STP iѕ issued and аllосаtеd tо Fоundеrѕ, Tеаm Inсеntivеѕ, аnd Initial Bank Rоll fund.</p>
              <p className="font-weight-bold economic-model__description pr-lg-1">STP is the token that will be used on the Bitgoals Platform. The token acts as:</p>
              <div className="row econmic-model__list">
                {
                  this.steps.map((step, key) => {
                    return (
                      <>
                        <div className="col-sm-6 economic-model__item" key={key}>
                          <div className="bg-white text-center economic-model__item-inner">
                            <i className={'economic-model__item-icon icon-' + step.icon}></i>
                            <p className="text-uppercase economic-model__item-description" dangerouslySetInnerHTML={{ __html: step.text }} />
                          </div>
                        </div>
                      </>
                    )
                  })
                }
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
