import './styles/main.scss';

import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import { Header } from './scripts/components/header.jsx';
import { HowItWorks } from './scripts/components/howitWorks.jsx';
import { EconomicModel } from './scripts/components/economicModel.jsx';
import { ForTheNews } from './scripts/components/forTheNews.jsx';
import { Footer } from './scripts/components/footer.jsx';

class App extends Component {
	render() {
		return (
			<div className="wrapper">
				<Header />
				<main id="main">
					<HowItWorks />
					<ForTheNews />
					<EconomicModel />
				</main>
				<Footer />
			</div>
		)
	}
}

ReactDOM.render(<App />, document.getElementById('root'));